.carouselitem{
    background-size: cover;
    object-position: center;
}


@media screen and (max-width: 980px) {
    .container{
        max-width: 900px;
    }
    p{
        font-size: 2.5rem;  
        margin-bottom: 32px;
    }
    h1{
        font-size: 3rem
    }
    .carouselp{
        font-size:2rem
    }
    h2{
        font-size: 4rem;
    }
    h3{
        font-size: 4rem;
    }
    h4{
        font-size: 4rem;
    }
    .display-4{
        font-size: 6rem;
    }
    .lead{
        font-size: 3rem;
        margin-top: 32px;
    }    
    .orangebg{
        font-size: 3rem;
        margin-top: 16px;
        border-radius: 15px;
        padding: 10px 30px
    }
    .icon{
        width: 600px;
        height: 600px
    }
    th{
        font-size: 2.5rem;
    }
    td{
        font-size: 2.5rem;
    }
    .navbar{
        padding-top: 12px;
        padding-bottom: 12px;
    }
    .coloredText{
        font-size: 3.5rem;
    }
    .logo{
        width: 100px;
        height: 100px;
        margin-right: 20px;
    }
    .nav-link{
        font-size: 2.8rem;
    }
    .navbar-toggler{
        width: 140px;
        height: 100px;     
        border-radius: 10px;
    }    
    .navbar-toggler-icon{
        width: 70px;
        height: 70px;
    }
    .form-label{
        font-size: 2.5rem;
    }
    .form-control{
        font-size: 2.5rem;
    }
    .form-select{
        font-size: 2.5rem;
    }
    .card-header{
        font-size: 3rem;
    }
    .card-title{
        font-size: 2rem;
    }
    .card-subtitle{
        font-size: 2rem;
    }
    .move{
        height: 25rem;
    }
    .caloriestat{
        font-size: 4rem;
    }
}

.orangebg{
    background-color: #FE6A16;
    color: white;
}

.orangebg:hover{
    background-color: #E83C00;
    color: white;
}
.orangebg:focus{
    background-color: #E83C00;
    color: white;
}
